<template>
  <BasicTable @register="templateDtlTable" class="w-4/4 xl:w-12/12" />
</template>
<script lang="ts" setup>
  import { BasicTable, useTable } from '@/components/Table';
  import { defHttp } from '@/utils/http/axios';
  import { ref } from 'vue';

  const safeCheckTemplateId = ref('');

  const reloadGrid = (param) => {
    if (safeCheckTemplateId.value != param.templateId) {
      safeCheckTemplateId.value = param.templateId;
      reload();
    }
  };

  const [templateDtlTable, { reload }] = useTable({
    title: '',
    api: async () => {
      let dataObj = await getSafeCheckTplDtl({
        safeCheckTemplateId: safeCheckTemplateId.value,
      });
      return dataObj.result;
    },
    columns: [
      {
        title: '检查项目',
        dataIndex: 'indexItem',
        width: 80,
      },
      {
        title: '检查内容',
        dataIndex: 'indexContent',
        width: 160,
        align: 'left',
      },
    ],
    bordered: true,
    pagination: false,
    useSearchForm: false,
    showTableSetting: false,
  });

  enum Api {
    getTplDtlData = '/po/safe-check-template/dtl/list',
  }

  function getSafeCheckTplDtl(params) {
    return defHttp.get({ url: Api.getTplDtlData, params });
  }
  defineExpose({ reloadGrid });
</script>
